/* Sevkiyat.css - Modern & Kompakt Tasarım */

/* ===== GENEL AYARLAR ===== */
* {
    box-sizing: border-box;
    
  }
  
  body {
    margin: 0;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    background: linear-gradient(135deg, #f8faff 0%, #f1f5ff 100%);
    color: #334155;
    line-height: 1.5;
  }
  
  .sevkiyat-container {
    padding: 20px;
    max-width: 100%;
    margin: 0 auto;
    min-height: 100vh;
  }
  
  /* ===== LOADING STATE ===== */
  .loading-state {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60vh;
    flex-direction: column;
  }
  
  .loading-spinner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  
  .spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #e2e8f0;
    border-top: 3px solid #3b82f6;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  /* ===== HEADER ===== */
  .sevkiyat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    padding: 0 4px;
  }
  
  .sevkiyat-title {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 0;
    font-size: 28px;
    font-weight: 700;
    color: #1e293b;
  }
  
  .title-icon {
    color: #3b82f6;
  }
  
  .mode-buttons {
    display: flex;
    gap: 8px;
    background: white;
    padding: 4px;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }
  
  .btn-mode {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 16px;
    border: none;
    border-radius: 8px;
    background: transparent;
    color: #64748b;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 14px;
  }
  
  .btn-mode:hover {
    background: #f1f5f9;
    color: #475569;
  }
  
  .btn-mode.active {
    background: #3b82f6;
    color: white;
    box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
  }
  
  /* ===== CONTROLS ===== */
  .sevkiyat-controls {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 24px;
  }
  
  .sevkiyat-search-bar {
    display: flex;
    align-items: center;
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    padding: 12px 16px;
    gap: 12px;
    max-width: 600px;
  }
  
  .search-icon {
    color: #94a3b8;
    flex-shrink: 0;
  }
  
  .sevkiyat-search-bar input {
    flex: 1;
    border: none;
    outline: none;
    font-size: 14px;
    color: #334155;
    background: transparent;
  }
  
  .sevkiyat-search-bar input::placeholder {
    color: #94a3b8;
  }
  
  .search-field-dropdown-wrapper {
    position: relative;
  }
  
  .search-field-button {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 8px 12px;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    background: #f8fafc;
    color: #475569;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .search-field-button:hover {
    border-color: #cbd5e1;
    background: #f1f5f9;
  }
  
  .search-field-dropdown {
    position: absolute;
    top: 100%;
    right: 0;
    background: white;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
    z-index: 100;
    min-width: 120px;
    margin-top: 4px;
    list-style: none;
    padding: 4px;
    margin: 4px 0 0 0;
  }
  
  .search-field-dropdown li {
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 6px;
    font-size: 13px;
    color: #475569;
    transition: all 0.2s ease;
  }
  
  .search-field-dropdown li:hover {
    background: #f1f5f9;
    color: #1e293b;
  }
  
  /* ===== BULK ACTIONS ===== */
  .bulk-actions-container {
    display: flex;
    align-items: center;
    gap: 12px;
    background: #3b82f6;
    color: white;
    padding: 12px 16px;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(59, 130, 246, 0.3);
  }
  
  .bulk-selection-info {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 500;
    font-size: 14px;
  }
  
  .bulk-icon {
    color: #dbeafe;
  }
  
  .btn-bulk-delete,
  .btn-bulk-clear {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 8px 12px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 500;
    font-size: 13px;
    transition: all 0.2s ease;
  }
  
  .btn-bulk-delete {
    background: #ef4444;
    color: white;
  }
  
  .btn-bulk-delete:hover {
    background: #dc2626;
  }
  
  .btn-bulk-clear {
    background: rgba(255, 255, 255, 0.2);
    color: white;
  }
  
  .btn-bulk-clear:hover {
    background: rgba(255, 255, 255, 0.3);
  }
  
  /* ===== MAIN CARD ===== */
  .sevkiyat-card {
    background: white;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
    overflow: hidden;
    margin-bottom: 24px;
  }
  
  .form-card {
    padding: 24px;
  }
  
  /* ===== TABLE ===== */
  .table-wrapper {
    overflow-x: auto;
  }
  
  .sevkiyat-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 14px;
  }
  
  .sevkiyat-table th {
    background: #f8fafc;
    padding: 16px 12px;
    text-align: left;
    font-weight: 600;
    color: #475569;
    border-bottom: 2px solid #e2e8f0;
    position: sticky;
    top: 0;
    z-index: 10;
  }
  
  .sevkiyat-table td {
    padding: 16px 12px;
    border-bottom: 1px solid #f1f5f9;
    color: #334155;
    vertical-align: middle;
  }
  
  .sevkiyat-table tbody tr:hover {
    background: #f8fafc;
  }
  
  .sevkiyat-no-badge {
    background: linear-gradient(135deg, #3b82f6, #1d4ed8);
    color: white;
    padding: 6px 12px;
    border-radius: 20px;
    font-weight: 600;
    font-size: 12px;
    display: inline-block;
    text-align: center;
    min-width: 60px;
  }
  
  .no-data {
    text-align: center;
    color: #94a3b8;
    font-style: italic;
    padding: 40px !important;
  }
  
  /* ===== CHECKBOX & SELECT ===== */
  .select-all-container {
    display: flex;
    justify-content: center;
  }
  
  .select-all-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 4px;
    border-radius: 4px;
    transition: all 0.2s ease;
  }
  
  .select-all-btn:hover {
    background: #f1f5f9;
  }
  
  .select-icon {
    color: #94a3b8;
  }
  
  .select-icon.active {
    color: #3b82f6;
  }
  
  .checkbox-wrapper {
    display: flex;
    justify-content: center;
  }


/* ===== CHECKBOX & SELECT (Devam) ===== */
.shipment-checkbox {
    width: 16px;
    height: 16px;
    cursor: pointer;
  }
  
  .checkbox-container {
    display: flex;
    align-items: center;
    gap: 6px;
    cursor: pointer;
  }
  
  .checkbox-icon {
    color: #94a3b8;
  }
  .checkbox-icon.active {
    color: #3b82f6;
  }
  
  /* ===== HEADER FILTER DROPDOWN ===== */
  .header-with-filter {
    display: flex;
    align-items: center;
    gap: 6px;
    cursor: pointer;
    position: relative;
    user-select: none;
  }
  
  .filter-icon {
    transition: transform 0.2s ease;
    color: #64748b;
  }
  
  .filter-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    background: white;
    border: 1px solid #e2e8f0;
    border-radius: 10px;
    box-shadow: 0 4px 16px rgba(0,0,0,0.12);
    z-index: 100;
    min-width: 160px;
    margin-top: 4px;
    padding: 4px 0;
  }
  
  .filter-options-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .filter-options-list li {
    padding: 8px 12px;
    transition: all 0.2s ease;
    border-radius: 6px;
  }
  
  .filter-options-list li:hover {
    background: #f1f5f9;
  }
  
  /* ===== ACTION BUTTONS ===== */
  .action-column {
    text-align: center;
    min-width: 160px;
  }
  
  .action-buttons {
    display: flex;
    gap: 6px;
    justify-content: center;
  }
  
  .btn-action {
    border: none;
    background: #f1f5f9;
    color: #475569;
    padding: 6px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  .btn-action:hover {
    background: #e2e8f0;
    color: #1e293b;
  }
  .btn-action.btn-view { color: #2563eb; }
  .btn-action.btn-edit { color: #10b981; }
  .btn-action.btn-pdf { color: #f59e0b; }
  .btn-action.btn-email { color: #0ea5e9; }
  .btn-action.btn-delete { color: #ef4444; }
  
  /* ===== FORM ===== */
  .form-title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 16px;
    color: #1e293b;
  }
  
  .form-group-inline {
    display: flex;
    flex-direction: column;
    gap: 6px;
    margin-bottom: 16px;
  }
  
  .form-control-compact {
    padding: 8px 12px;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    font-size: 14px;
  }
  
  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
  }
  
  .section-title {
    font-size: 16px;
    font-weight: 600;
    color: #334155;
  }
  
  .btn-add-row-compact {
    display: flex;
    align-items: center;
    gap: 6px;
    background: #3b82f6;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 13px;
    transition: all 0.2s ease;
  }
  .btn-add-row-compact:hover { background: #2563eb; }
  
  .compact-table-wrapper {
    overflow-x: auto;
  }
  
  .compact-form-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 13px;
  }
  
  .compact-form-table th,
  .compact-form-table td {
    border: 1px solid #f1f5f9;
    padding: 8px;
    text-align: center;
  }
  
  .compact-input {
    width: 100%;
    padding: 6px 8px;
    border: 1px solid #e2e8f0;
    border-radius: 6px;
    font-size: 13px;
  }
  
  .beden-input {
    width: 50px;
    text-align: center;
    padding: 4px;
  }
  
  .btn-remove-row {
    border: none;
    background: #ef4444;
    color: white;
    padding: 4px 6px;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  .btn-remove-row:hover { background: #dc2626; }
  
  /* ===== FORM FOOTER ===== */
  .form-actions-bottom {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .total-amount {
    font-size: 16px;
    font-weight: 600;
    color: #1e293b;
  }
  
  .total-amount span {
    color: #3b82f6;
  }
  
  .button-group {
    display: flex;
    gap: 12px;
  }
  
  .btn-form {
    padding: 8px 16px;
    border-radius: 8px;
    border: none;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .btn-form.btn-primary {
    background: #3b82f6;
    color: white;
  }
  .btn-form.btn-primary:hover { background: #2563eb; }
  
  .btn-form.btn-secondary {
    background: #f1f5f9;
    color: #334155;
  }
  .btn-form.btn-secondary:hover { background: #e2e8f0; }
  
  /* ===== MODAL ===== */
  .modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 40px;
    z-index: 200;
    overflow-y: auto;
  }
  
  .modal-content {
    background: white;
    border-radius: 16px;
    padding: 24px;
    position: relative;
    max-width: 1200px;
    width: 100%;
  }
  
  .modal-fullwidth {
    width: 100%;
  }
  
  .modal-close-btn {
    position: absolute;
    top: 12px;
    right: 12px;
    background: none;
    border: none;
    cursor: pointer;
    color: #ef4444;
  }
  
  .modal-title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 16px;
  }
  
  .modal-info-section p {
    margin: 4px 0;
    font-size: 14px;
  }
  
  .modal-table-container {
    overflow-x: auto;
    margin-top: 20px;
  }
  
  .modal-table-fullwidth {
    width: 100%;
    border-collapse: collapse;
    font-size: 13px;
  }
  
  .modal-table-fullwidth th,
  .modal-table-fullwidth td {
    border: 1px solid #f1f5f9;
    padding: 8px;
    text-align: center;
  }
  
  .modal-footer {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
  }
  
  .modal-actions {
    display: flex;
    gap: 10px;
  }
  
  .btn-icon-with-text {
    display: flex;
    align-items: center;
    gap: 6px;
    border: none;
    border-radius: 8px;
    padding: 8px 14px;
    cursor: pointer;
    font-size: 13px;
    font-weight: 500;
    transition: all 0.2s ease;
  }
  
  .btn-icon-with-text.btn-pdf {
    background: #f59e0b;
    color: white;
  }
  .btn-icon-with-text.btn-pdf:hover { background: #d97706; }
  
  .btn-icon-with-text.btn-email {
    background: #0ea5e9;
    color: white;
  }
  .btn-icon-with-text.btn-email:hover { background: #0284c7; }
  