/* ========================
 * Gelişmiş Stil ve Değişkenler
 * ======================== */
 :root {
  --primary-color: #4f46e5; /* İndigo */
  --primary-color-dark: #3730a3;
  --primary-color-light: #6366f1;
  --secondary-color: #6b7280;
  --secondary-color-dark: #4b5563;
  --success-color: #059669;
  --success-color-light: #10b981;
  --danger-color: #dc2626;
  --danger-color-dark: #b91c1c;
  --warning-color: #f59e0b;
  --info-color: #0ea5e9;

  --bg-color: #f8fafc;
  --bg-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --card-bg-color: #ffffff;
  --card-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
  --border-color: #e2e8f0;
  --text-color: #1e293b;
  --text-color-light: #64748b;
  --text-color-muted: #94a3b8;

  --shadow-sm: 0 2px 4px rgba(0, 0, 0, 0.04);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 10px 40px rgba(0, 0, 0, 0.12);
  --shadow-xl: 0 20px 60px rgba(0, 0, 0, 0.15);
  
  --border-radius-sm: 6px;
  --border-radius-base: 10px;
  --border-radius-lg: 16px;
  --border-radius-xl: 20px;
  --transition-speed: 0.2s;
  --transition-smooth: cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  background: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

/* ========================
 * Kapsayıcı ve Ana Layout
 * ======================== */
.urun-satis-container {
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 100vh;
}

.urun-satis-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  padding: 24px;
  background: var(--card-bg-color);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
}

.urun-satis-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--bg-gradient);
}

.urun-satis-title-container h1 {
  font-size: 2.25rem;
  font-weight: 800;
  color: var(--text-color);
  margin: 0 0 8px 0;
  background: var(--bg-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.urun-satis-title-container p {
  font-size: 1.1rem;
  color: var(--text-color-light);
  margin: 0;
}

.urun-satis-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

/* ========================
 * Gelişmiş Butonlar
 * ======================== */
.urun-satis-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  border-radius: var(--border-radius-base);
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all var(--transition-speed) var(--transition-smooth);
  border: 1px solid transparent;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.urun-satis-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s ease;
}

.urun-satis-btn:hover::before {
  left: 100%;
}

.urun-satis-btn-primary {
  background: var(--bg-gradient);
  color: white;
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.urun-satis-btn-secondary {
  background-color: var(--card-bg-color);
  color: var(--secondary-color);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
}

.urun-satis-btn-secondary:hover {
  background-color: var(--bg-color);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-success {
  background: linear-gradient(135deg, var(--success-color) 0%, var(--success-color-light) 100%);
  color: white;
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-success:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.urun-satis-btn-danger {
  background: linear-gradient(135deg, var(--danger-color) 0%, var(--danger-color-dark) 100%);
  color: white;
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-danger:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.urun-satis-btn-warning {
  background: linear-gradient(135deg, var(--warning-color) 0%, #f97316 100%);
  color: white;
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-warning:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.urun-satis-btn-info {
  background: linear-gradient(135deg, var(--info-color) 0%, #0284c7 100%);
  color: white;
  box-shadow: var(--shadow-md);
}

.urun-satis-btn-info:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* ========================
 * Export Buttons Container
 * ======================== */
.export-buttons {
  display: flex;
  gap: 8px;
  margin-left: auto;
}

.export-buttons .urun-satis-btn {
  padding: 10px 16px;
  font-size: 0.9rem;
}

/* ========================
 * Liste Görünümü
 * ======================== */
.urun-satis-list-view {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.search-filter-container {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  align-items: center;
  background: var(--card-bg-color);
  padding: 20px;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
}

.search-bar {
  display: flex;
  align-items: center;
  flex: 1;
  min-width: 300px;
  padding: 12px 16px;
  background-color: var(--bg-color);
  border: 2px solid var(--border-color);
  border-radius: var(--border-radius-base);
  transition: all var(--transition-speed) var(--transition-smooth);
}

.search-bar:focus-within {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.search-bar svg {
  color: var(--text-color-muted);
  margin-right: 12px;
}

.search-bar input {
  flex: 1;
  border: none;
  background: transparent;
  outline: none;
  font-size: 1rem;
  color: var(--text-color);
}

.search-bar input::placeholder {
  color: var(--text-color-muted);
}

.search-field-dropdown-container {
  position: relative;
}

.search-field-button {
  background-color: var(--card-bg-color);
  border: 2px solid var(--border-color);
  color: var(--text-color);
  padding: 12px 16px;
  border-radius: var(--border-radius-base);
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
  transition: all var(--transition-speed) var(--transition-smooth);
}

.search-field-button:hover {
  border-color: var(--primary-color);
  background-color: var(--bg-color);
}

.search-field-dropdown {
  position: absolute;
  top: calc(100% + 4px);
  right: 0;
  background-color: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-base);
  box-shadow: var(--shadow-lg);
  z-index: 20;
  min-width: 160px;
  overflow: hidden;
}

.search-field-dropdown div {
  padding: 12px 16px;
  cursor: pointer;
  transition: background-color var(--transition-speed);
  border-bottom: 1px solid var(--border-color);
}

.search-field-dropdown div:last-child {
  border-bottom: none;
}

.search-field-dropdown div:hover {
  background-color: var(--bg-color);
  color: var(--primary-color);
}

.filter-dropdown-container {
  position: relative;
}

.filter-options {
  position: absolute;
  top: calc(100% + 4px);
  right: 0;
  background-color: var(--card-bg-color);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-base);
  box-shadow: var(--shadow-lg);
  z-index: 20;
  padding: 16px;
  min-width: 200px;
}

.filter-options label {
  display: flex;
  align-items: center;
  padding: 8px 0;
  cursor: pointer;
  transition: color var(--transition-speed);
}

.filter-options label:hover {
  color: var(--primary-color);
}

.filter-options input[type="checkbox"] {
  margin-right: 12px;
  width: 16px;
  height: 16px;
  accent-color: var(--primary-color);
}

/* ========================
 * Gelişmiş Tablo Tasarımı
 * ======================== */
.urun-satis-table-container {
  background-color: var(--card-bg-color);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-md);
  overflow: hidden;
  border: 1px solid var(--border-color);
}

.urun-satis-table {
  width: 100%;
  border-collapse: collapse;
}

.urun-satis-table th {
  padding: 20px 24px;
  text-align: left;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  font-weight: 700;
  color: var(--text-color);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 2px solid var(--border-color);
}

.urun-satis-table td {
  padding: 20px 24px;
  border-bottom: 1px solid var(--border-color);
  vertical-align: middle;
}

.urun-satis-table tbody tr {
  transition: all var(--transition-speed) var(--transition-smooth);
}

.urun-satis-table tbody tr:hover {
  background: linear-gradient(135deg, rgba(79, 70, 229, 0.02) 0%, rgba(79, 70, 229, 0.04) 100%);
  transform: translateY(-1px);
}

.urun-satis-table tbody tr:last-child td {
  border-bottom: none;
}

.urun-satis-actions-cell {
  white-space: nowrap;
}

.urun-satis-actions-cell button {
  background: none;
  border: none;
  cursor: pointer;
  margin: 0 4px;
  padding: 8px;
  border-radius: var(--border-radius-sm);
  color: var(--secondary-color);
  transition: all var(--transition-speed) var(--transition-smooth);
}

.urun-satis-actions-cell button:hover {
  background-color: var(--bg-color);
  transform: scale(1.1);
}

.urun-satis-actions-cell button:nth-child(1):hover {
  color: var(--info-color);
}

.urun-satis-actions-cell button:nth-child(2):hover {
  color: var(--primary-color);
}

.urun-satis-actions-cell button:nth-child(3):hover {
  color: var(--danger-color);
}

.no-data {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-color-muted);
  font-size: 1.1rem;
}

/* ========================
 * Gelişmiş Form Tasarımı
 * ======================== */
.urun-satis-form-view {
  background: var(--card-bg-color);
  padding: 32px;
  border-radius: var(--border-radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  position: relative;
  overflow: hidden;
}

.urun-satis-form-view::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--bg-gradient);
}

.urun-satis-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-group-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
}

.form-group label {
  display: block;
  font-weight: 600;
  margin-bottom: 8px;
  color: var(--text-color);
  font-size: 0.95rem;
}

.form-control {
  width: 100%;
  padding: 14px 16px;
  border: 2px solid var(--border-color);
  border-radius: var(--border-radius-base);
  background-color: var(--bg-color);
  color: var(--text-color);
  font-size: 1rem;
  transition: all var(--transition-speed) var(--transition-smooth);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  background-color: var(--card-bg-color);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

/* ========================
 * Ürün Listesi Tasarımı
 * ======================== */
.urun-list-container {
  margin-top: 24px;
  border: 2px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  background-color: var(--card-bg-color);
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

.urun-list-container h3 {
  margin: 0;
  padding: 20px 24px;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-color);
  border-bottom: 2px solid var(--border-color);
}

.urun-list-headers {
  display: grid;
  grid-template-columns: 2fr 3fr 1fr 1fr 1fr 60px;
  gap: 16px;
  padding: 16px 24px;
  font-weight: 600;
  color: var(--secondary-color);
  background-color: var(--bg-color);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.urun-list-body {
  padding: 24px;
}

.urun-row {
  display: grid;
  grid-template-columns: 2fr 3fr 1fr 1fr 1fr 60px;
  gap: 16px;
  margin-bottom: 16px;
  align-items: center;
  padding: 12px;
  border-radius: var(--border-radius-base);
  transition: background-color var(--transition-speed);
}

.urun-row:hover {
  background-color: var(--bg-color);
}

.urun-row input {
  width: 100%;
  padding: 12px 14px;
  border: 2px solid var(--border-color);
  border-radius: var(--border-radius-sm);
  background-color: var(--card-bg-color);
  font-size: 0.95rem;
  transition: all var(--transition-speed) var(--transition-smooth);
}

.urun-row input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.urun-total {
  font-weight: 700;
  color: var(--success-color);
  text-align: right;
  font-size: 1rem;
}

.remove-urun-btn {
  background: none;
  border: none;
  color: var(--danger-color);
  cursor: pointer;
  padding: 8px;
  border-radius: var(--border-radius-sm);
  transition: all var(--transition-speed) var(--transition-smooth);
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-urun-btn:hover {
  background-color: rgba(220, 38, 38, 0.1);
  transform: scale(1.1);
}

.urun-list-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-top: 2px solid var(--border-color);
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
}

.add-urun-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  background: none;
  border: 2px dashed var(--primary-color);
  color: var(--primary-color);
  padding: 12px 20px;
  border-radius: var(--border-radius-base);
  cursor: pointer;
  font-weight: 600;
  transition: all var(--transition-speed) var(--transition-smooth);
}

.add-urun-btn:hover {
  background: var(--bg-gradient);
  color: white;
  border-style: solid;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.total-amount {
  font-size: 1.4rem;
  font-weight: 800;
  color: var(--success-color);
  text-align: right;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  margin-top: 32px;
  padding-top: 24px;
  border-top: 2px solid var(--border-color);
}

/* ========================
 * Gelişmiş Modallar
 * ======================== */
.urun-satis-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s var(--transition-smooth);
}

.urun-satis-modal-content {
  background: var(--card-bg-color);
  padding: 32px;
  border-radius: var(--border-radius-xl);
  box-shadow: var(--shadow-xl);
  max-width: 700px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  border: 1px solid var(--border-color);
}

.urun-satis-modal-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--bg-gradient);
  border-radius: var(--border-radius-xl) var(--border-radius-xl) 0 0;
}

.urun-satis-modal-content h3 {
  margin-top: 0;
  margin-bottom: 16px;
  background: var(--bg-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 1.5rem;
  font-weight: 800;
}

.urun-satis-modal-content p {
  font-size: 1.1rem;
  color: var(--text-color-light);
  line-height: 1.6;
}

.urun-satis-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
}

.satis-details {
  background: var(--bg-color);
  padding: 20px;
  border-radius: var(--border-radius-base);
  margin-bottom: 20px;
}

.satis-details p {
  margin: 8px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.satis-details strong {
  color: var(--text-color);
  font-weight: 600;
  min-width: 120px;
}

/* ========================
 * Mail Confirmation Modal
 * ======================== */
.mail-confirmation-modal .urun-satis-modal-content {
  max-width: 500px;
}

.mail-confirmation-content {
  text-align: center;
  padding: 20px 0;
}

.mail-confirmation-content .mail-icon {
  background: var(--bg-gradient);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
}

.mail-confirmation-content .mail-icon svg {
  color: white;
  width: 40px;
  height: 40px;
}

/* ========================
 * Success Messages & Loading
 * ======================== */
.loading-spinner {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255,255,255,0.3);
  border-radius: 50%;
  border-top: 2px solid white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ========================
 * Status Badges
 * ======================== */
.status-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.sent {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
}

.status-badge.pending {
  background: rgba(245, 158, 11, 0.1);
  color: var(--warning-color);
}

/* ========================
 * Responsive Design
 * ======================== */
@media (max-width: 1024px) {
  .urun-satis-container {
    padding: 16px;
  }
  
  .search-filter-container {
    flex-direction: column;
    align-items: stretch;
  }
  
  .export-buttons {
    margin-left: 0;
    width: 100%;
  }
}

@media (max-width: 768px) {
  .urun-satis-header {
    flex-direction: column;
    align-items: stretch;
    gap: 16px;
    padding: 20px;
  }
  
  .urun-satis-title-container h1 {
    font-size: 1.8rem;
  }
  
  .urun-satis-actions {
    flex-direction: column;
  }
  
  .search-bar {
    min-width: auto;
  }
  
  .form-group-row {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .urun-list-headers,
  .urun-row {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  
  .urun-list-headers {
    display: none;
  }
  
  .urun-row {
    background: var(--bg-color);
    padding: 16px;
    border-radius: var(--border-radius-base);
    margin-bottom: 12px;
  }
  
  .urun-list-body {
    padding: 16px;
  }
  
  .urun-list-footer {
    flex-direction: column;
    gap: 16px;
    align-items: stretch;
  }
  
  .total-amount {
    text-align: center;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .urun-satis-modal-content {
    margin: 20px;
    width: calc(100% - 40px);
    padding: 24px;
  }
}

@media (max-width: 640px) {
  .urun-satis-table-container {
    overflow-x: auto;
  }
  
  .urun-satis-table {
    min-width: 600px;
  }
}

/* ========================
 * Animations
 * ======================== */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.urun-satis-table tbody tr {
  animation: slideUp 0.3s var(--transition-smooth);
}

.urun-satis-form-view,
.urun-satis-list-view > * {
  animation: slideUp 0.4s var(--transition-smooth);
}

/* ========================
 * Print Styles
 * ======================== */
@media print {
  .urun-satis-actions,
  .search-filter-container,
  .urun-satis-actions-cell,
  .export-buttons {
    display: none !important;
  }
  
  .urun-satis-container {
    padding: 0;
    max-width: none;
  }
  
  .urun-satis-table-container {
    box-shadow: none;
    border: 1px solid #ddd;
  }
}