/* Sidebar'ın ana konteyneri */
.sidebar-container {
    width: 250px;
    height: 100vh;
    background-color: #f5f5f7;
    color: #1c1c1e;
    display: flex;
    flex-direction: column;
    padding: 15px;
    box-shadow: 1px 0 10px rgba(0, 0, 0, 0.05);
}

/* Logo bölümü */
.logo-section {
    padding-bottom: 15px;
    border-bottom: 1px solid #e0e0e0;
    text-align: left;
    margin-bottom: 15px;
    position: relative;
    overflow: hidden;
}

/* BozkurtSan logosu metni */
.logo-text {
    font-family: 'Poppins', sans-serif; /* Yeni font ekledik */
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 0.5px;
    color: #1c1c1e;
    position: relative;
    z-index: 2;
}

@keyframes bounceIn {
    0% { transform: scale(0.3); opacity: 0; }
    50% { transform: scale(1.1); opacity: 1; }
    70% { transform: scale(0.9); }
    100% { transform: scale(1); }
}

/* Parıldama animasyonu - Bu artık gereksiz, daha basit bir bounceIn yeterli */
/* Bu kod bloğunu sildim, çünkü logoyu karmaşıklaştırıp görünmez yapabiliyordu. */

/* Navigasyon menüsü */
.nav-section {
    flex-grow: 1; /* Menünün kalan tüm boşluğu doldurmasını sağlar */
}

.nav-section ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.nav-section li a {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 12px 15px;
    text-decoration: none;
    color: #555555;
    font-size: 16px;
    font-weight: 500;
    border-radius: 8px;
    transition: all 0.2s ease;
}

.nav-section li a:hover {
    background-color: #e0e0e0;
    color: #000000;
}

/* Aktif menü öğesi */
.nav-section li a.active {
    background-color: #007aff;
    color: #ffffff;
    font-weight: 600;
    box-shadow: 0 2px 8px rgba(0, 122, 255, 0.3);
}

.nav-section li a.active svg {
    color: #ffffff;
}

/* Menü elemanlarının animasyonu */
.nav-section li {
    animation: fadeInSlideIn 0.5s ease-out forwards;
    opacity: 0;
}

.nav-section li:not(:last-child) {
    margin-bottom: 5px;
}

@keyframes fadeInSlideIn {
    from { opacity: 0; transform: translateX(-10px); }
    to { opacity: 1; transform: translateX(0); }
}

/* Çıkış butonu bölümü */
.logout-section {
    padding-top: 15px;
    border-top: 1px solid #e0e0e0;
    margin-top: 15px;
}

.logout-button {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 12px 15px;
    background-color: #ff3b30;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s ease;
}

.logout-button:hover {
    background-color: #c0392b;
    box-shadow: 0 2px 8px rgba(255, 59, 48, 0.3);
}

