html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden; /* kayma yok */
    font-family: "Inter", Arial, sans-serif;
    background: #f9fafb;
    color: #1e293b;
  }
  
  .dashboard {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  
  /* Header */
  .header {
    text-align: center;
    padding: 1rem;
    background: white;
    border-bottom: 1px solid #e5e7eb;
  }
  
  .title {
    margin: 0;
    font-size: 1.8rem;
    font-weight: 700;
    color: #111827;
  }
  
  .datetime {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    align-items: center;
    margin-top: 0.5rem;
    font-size: 0.85rem;
    color: #6b7280;
  }
  
  /* Grid */
  .grid {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1rem;
    padding: 1rem;
    box-sizing: border-box;
  }
  
  /* Card */
  .card {
    background: white;
    border-radius: 0.75rem;
    padding: 1rem;
    box-shadow: 0 2px 8px rgba(0,0,0,0.05);
    display: flex;
    flex-direction: column;
  }
  
  .card-header {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
    color: #111827;
  }
  
  /* Calendar */
  .calendar-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem 0;
  }
  
  .calendar-controls button {
    background: #e5e7eb;
    border: none;
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    cursor: pointer;
  }
  
  .calendar {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }
  
  .weekdays, .week {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    text-align: center;
    font-size: 0.8rem;
  }
  
  .day {
    padding: 0.5rem;
    cursor: pointer;
    border-radius: 0.25rem;
  }
  
  .day:hover {
    background: #f3f4f6;
  }
  
  .day.selected {
    background: #3b82f6;
    color: white;
  }
  
  /* Currency Cards */
  .currency-list {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .currency-card {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    background: #f9fafb;
    border: 1px solid #e5e7eb;
    border-radius: 0.75rem;
    padding: 0.75rem 1rem;
    box-shadow: 0 1px 4px rgba(0,0,0,0.05);
  }
  
  .currency-card .symbol {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-weight: bold;
  }
  
  .currency-card.usd .symbol { background: #3b82f6; }
  .currency-card.eur .symbol { background: #f59e0b; }
  .currency-card.gbp .symbol { background: #8b5cf6; }
  
  .currency-card .rate {
    font-size: 1.2rem;
    font-weight: 700;
    color: #111827;
  }
  
  .currency-card .label {
    font-size: 0.75rem;
    color: #6b7280;
  }
  
  /* Notes */
  textarea {
    resize: none;
    border: 1px solid #e5e7eb;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    font-family: inherit;
  }
  
  button {
    align-self: flex-start;
    background: #3b82f6;
    color: white;
    border: none;
    padding: 0.4rem 0.8rem;
    border-radius: 0.5rem;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  button:hover {
    background: #2563eb;
  }
  
  .notes {
    margin-top: 0.5rem;
    overflow-y: auto;
    flex: 1;
  }
  
  .note {
    background: #f9fafb;
    border: 1px solid #e5e7eb;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
  
  .note span {
    font-size: 0.75rem;
    color: #6b7280;
  }
  